import React, { useState } from "react";
import logo from "./logo.svg";
import "./App.css";
import dxrmb from "dxrmb";
import { App, Input, Space, Typography, message } from "antd";

export default function MyApp() {
  const [dxrmbVal, setDxrmbVal] = useState("");

  function onChangeXx(e: any) {
    try {
      const val = e.target.value;
      const dxrmbVal = dxrmb(val);
      setDxrmbVal(dxrmbVal);
    } catch (error: any) {
      message.error(error.message);
    }
  }

  return (
    <App style={{ padding: "1em" }}>
      <h1>人民币大写转换</h1>

      <Space direction="vertical" style={{ width: "100%" }}>
        <Input
          prefix="￥"
          suffix="RMB"
          placeholder="请输入小写人民币"
          onChange={onChangeXx}
        />
        <Input.TextArea
          placeholder="转换后的大写金额"
          value={dxrmbVal}
        ></Input.TextArea>
      </Space>
    </App>
  );
}
